import { useParams } from 'react-router-dom'
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Card, Container} from 'react-bootstrap';
import noimg from '../Assets/images/noimg.jpg';

const Detail = () => {
    const [article, setArticle] = useState(null);
    const {id} = useParams();
    console.log(id);
    useEffect(() => {
        const fetchArticle = async () => {
          try {
            const response = await axios.get(`https://junkawaydubai.com/api/get_articleByID.php?id=${id}`);
            setArticle(response.data);
          } catch (error) {
            console.error('There was an error fetching the article!', error);
          }
        };
    
        fetchArticle();
      }, [id]);
    
  return (
    <Container className='h-full'>
      {article ? (
        <Card className="my-4">
          <Card.Header>{article.article_title}</Card.Header>
          <Card.Body>
            <Card.Text>{article.description}</Card.Text>
        <img src={article.imageurl?article.imageurl:noimg} alt={article.article_title} style={{ width: '100px', height: '100px' }} />
            <p><strong>Publisher:</strong> {article.publisher_name}</p>
            <p><strong>Status:</strong> {article.status}</p>
          </Card.Body>
        </Card>
      ) : (
        <p>Loading...</p>
      )}
    </Container>
  )
}

export default Detail
