import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { Link, useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import Signup from './Signup';
const Login = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [submit, setSubmit] = useState(false);
    useEffect(() => {
        if (submit) {
            const loginData = { email, password };

            const sendLoginData = async () => {
                try {
                    const response = await axios.post('https://junkawaydubai.com/api/login.php', loginData, {
                        headers: {
                            'Content-Type': 'application/json',
                        },
                    });
                    if (response.data.message==='Login successful' && response.data.email===email) {
                        toast.success('Login successful!', {
                            position: "top-right",
                            autoClose: 3000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                        });
                        console.log('Success:', response.data);  
                        localStorage.setItem("email", response.data.email);
                        setTimeout(() => navigate('/articlePost'), 5000);
                    }
                    else{
                        toast.error('Invalid Email & password....!', {
                            position: "top-right",
                            autoClose: 3000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                        });
                    }
                   
                } catch (error) {
                    toast.error('Signup failed. Please try again.', {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    console.error('Error:', error);
                } finally {
                    setSubmit(false);
                }
            };

            sendLoginData();
        }
    }, [submit, email, password]);

    const handleSubmit = (event) => {
        event.preventDefault();
        setSubmit(true);
    };
    return (
        <div className='cntr d-flex flex-column gap-3  justify-content-center align-items-lg-center' style={{ "height": '79vh' }}>
        <h1 className='text-center'>Login Here</h1>
            <Form className='d-flex flex-column justify-content-center text-center gap-4 bg-light shadow-lg p-3 m-3 m-md-3 rounded-3' onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formBasicEmail">

                    <Form.Control type="email" placeholder="Enter email" value={email}
                        onChange={(e) => setEmail(e.target.value)} required />

                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">

                    <Form.Control type="password" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)} required />
                </Form.Group>
                <Button variant="danger" type="submit">
                    Submit
                </Button>
                <Link variant="danger" to='/SignUp'>
                    Sign Up
                </Link>
                <ToastContainer />
            </Form>

        </div>
    )
}

export default Login
