import React from 'react'
import {Route, Routes } from 'react-router-dom'
import "bootstrap/dist/css/bootstrap.min.css";
import Layout from '../Shared/layout'
import NewsContainer from '../Components/NewsContainer'
import Login from '../Components/Login';
import Protected from './Protected';
import Signup from '../Components/Signup';
import ArticlePost from '../Components/ArticlePost';
import ArticleShow from './../Components/ArticleShow';
import Detail from '../Components/Detail';

const index = () => {
  return (
    <Layout>
      <Routes>
        <Route path='/' element={<NewsContainer pageSize={6} country={'us'} count={'general'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/business' element={<NewsContainer pageSize={6} country={'us'} category={'business'} />} />
      </Routes>
      <Routes>
        <Route path='/entertainment' element={<NewsContainer pageSize={6} country={'in'} category={'entertainment'} />} />
      </Routes>
      <Routes>
        <Route path='/health' element={<NewsContainer pageSize={6} country={'us'} category={'health'} />} />
      </Routes>
      <Routes>
        <Route path='/science' element={<NewsContainer pageSize={6} country={'us'} category={'science'} />} />
      </Routes>
      <Routes>
        <Route path='/sports' element={<NewsContainer pageSize={6} country={'us'} category={'sports'} />} />
      </Routes>
      <Routes>
        <Route path='/technology' element={<NewsContainer pageSize={6} country={'us'} category={'technology'} />} />
      </Routes>
      <Routes>
        <Route path='/china' element={<NewsContainer pageSize={6} country={'cn'} count={'china'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/germany' element={<NewsContainer pageSize={6} country={'de'} count={'germany'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/India' element={<NewsContainer pageSize={6} country={'in'} count={'India'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/Turkey' element={<NewsContainer pageSize={6} country={'tr'} count={'Turkey'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/United-States' element={<NewsContainer pageSize={6} country={'us'} count={'United-States'} category={'general'} />} />
      </Routes>
      <Routes>
        <Route path='/login' element={<Login />} />
      </Routes>
      <Routes>
        <Route path='/SignUp' element={<Signup />} />
      </Routes>
      <Routes>
        <Route path='/articlePost' element={<Protected Component={<ArticlePost />} />} />
      </Routes>
      <Routes>
        <Route path='/ArticleShow' element={<ArticleShow />} />
      </Routes>
      <Routes>
      <Route path='/detail/:id' element={<Detail/>} />
    </Routes>
    </Layout>
  )
}

export default index
