import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Link,  useLocation, useNavigate } from 'react-router-dom';
import img from '../../Assets/images/images.png';
import ArticleShow from './../../Components/ArticleShow';

function MyNavbar() {
    const location = useLocation();
    const navigate = useNavigate();
    const mydata = localStorage.getItem('email');
  
    console.log(mydata);
const isActive = (path) => location.pathname === path;
const linkStyle = (path) => ({
    color: isActive(path) ? '#8B0000' : 'black', 
    textDecoration:   'none'
  });
  function handleLogIn(){
navigate('/login')
  }
  function handleLogOut(){
localStorage.clear();
navigate('/')
}

    return (
    <>
    <Navbar collapseOnSelect expand="lg" className='bg-danger bg-gradient'>
      <Container>
        <Navbar.Brand>
          <Link className='text-decoration-none text-danger' to='/'>
            <img src={img} alt="logo" width='50px' style={{ mixBlendMode: 'multiply' }} />
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link>
              <Link className='text-decoration-none' style={linkStyle('/')} to='/'>Home</Link>
            </Nav.Link>
            <NavDropdown title="Genre" className='text-white' id="collapsible-nav-dropdown">
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/business')} to="/business">Business</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/entertainment')} to="/entertainment">Entertainment</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/health')} to="/health">Health</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/science')} to="/science">Science</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/sports')} to="/sports">Sports</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/technology')} to="/technology">Technology</Link>
              </NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="Country" className='text-white' id="collapsible-nav-dropdown">
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/china')} to="/china">China</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/Germany')} to="/Germany">Germany</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/United-States')} to="/United-States">United States</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/India')} to="/India">India</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
              <NavDropdown.Item>
                <Link className='text-decoration-none' style={linkStyle('/Turkey')} to="/Turkey">Turkey</Link>
              </NavDropdown.Item>
              <NavDropdown.Divider className='bg-danger'/>
            </NavDropdown>
          {mydata&&  <Nav.Link>
              <Link className='text-decoration-none' style={linkStyle('/ArticlePost')} to='/ArticlePost'>Submit Articles</Link>
            </Nav.Link>}
            <Nav.Link>
            <Link className='text-decoration-none' style={linkStyle('/ArticleShow')} to='/ArticleShow'>ArticleShow</Link>
          </Nav.Link>
          </Nav>
        {
          !mydata ?  <div className='mt-2 md:mt-0'>
            <Link className='btn btn-light text-decoration-none' onClick={handleLogIn} to='/login'>LogIn To Publish</Link>
          </div> :  <div className='mt-2 md:mt-0'>
            <Link className='btn btn-light text-decoration-none' title='logOut'   onClick={handleLogOut} to='/login'>{mydata&&mydata}</Link>
          </div>
        }
        </Navbar.Collapse>
      </Container>
      </Navbar>
    </>

    );
}

export default MyNavbar;