import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import React, { useEffect, useState } from 'react'
import { Form, Button, Container } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
const ArticlePost = () => {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    imgurl: '',
    publisherName: ''
  });
  const [submitFlag, setSubmitFlag] = useState(false);
const navigate = useNavigate();
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  useEffect(() => {
    if (submitFlag) {
    const postArticle = async () => {
      try {
        const response = await axios.post('https://junkawaydubai.com/api/post_article.php', {
          article_title: formData.title,
          description: formData.description,
          imageurl: formData.imgurl,
          publisher_name: formData.publisherName,
          publisher_email: localStorage.getItem('email'),
          status: 'draft'
        });
        console.log('Response:', response.data);
        toast.success('Article submitted successfully', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
      });
      setTimeout(() => navigate('/'), 5000);
      
      } catch (error) {
        console.error('There was an error submitting the form!', error);
        toast.error('There is an error for submitting an article....!', {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
      });
      }
    };
    if (formData.title && formData.description && formData.publisherName) {
      postArticle();
      setSubmitFlag(false);
    }
  }
  }, [submitFlag,formData]);
  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitFlag(true); // Set the flag to trigger the API call
  };
  return (
    <Container className=' d-flex flex-column gap-1'>
      <h1 className="my-4">Submit an Article</h1>
      <Form onSubmit={handleSubmit} >
        <Form.Group className='my-2' controlId="formTitle">
          <Form.Label>Title</Form.Label>
          <Form.Control
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            placeholder="Enter article title"
            required
          />
        </Form.Group>
        <Form.Group className='my-2' controlId="formDescription">
          <Form.Label>Description</Form.Label>
          <Form.Control
            as="textarea"
            name="description"
            value={formData.description}
            onChange={handleChange}
            placeholder="Enter article description"
            rows={3}
            required
          />
        </Form.Group>

        <Form.Group className='my-2' controlId="formImgURL">
          <Form.Label>Image URL</Form.Label>
          <Form.Control
            type="text"
            name="imgurl"
            value={formData.imgurl}
            onChange={handleChange}
            placeholder="Enter image URL"
          />
        </Form.Group>

        <Form.Group className='my-2' controlId="formPublisherName">
          <Form.Label>Publisher Name</Form.Label>
          <Form.Control
            type="text"
            name="publisherName"
            value={formData.publisherName}
            onChange={handleChange}
            placeholder="Enter publisher name"
            required
          />
        </Form.Group>

        <Button variant="danger" type="submit" className='my-3'>
          Submit
        </Button>
      </Form>
      <ToastContainer />
    </Container>
  )
}

export default ArticlePost
