import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Card from 'react-bootstrap/Card';
import noimage from '../Assets/images/noimg.jpg';
import { Link } from 'react-router-dom';

const ArticleShow = () => {
  const [articles, setArticles] = useState([]);

  useEffect(() => {
    const fetchArticles = async () => {
      try {
        const response = await axios.get('https://junkawaydubai.com/api/get_articles.php');
        setArticles(response.data.articles);
      } catch (error) {
        console.error('There was an error fetching the articles!', error);
      }
    };

    fetchArticles();
  }, []);
  
  console.log(articles);
  
  return (
    <div className='container-lg my-5 d-flex flex-column cntr'>
      <h1 className="my-4">Article List</h1>
      <div className="row bg-black d-flex gap-1 px-3">
        {
          articles
            .filter(article => article.status === 'ok')
            .map((item) => (
              <Card key={item.article_id} style={{ width: '22rem' }} className='bg-light my-3 text-dark shadow-lg crd'>
                <Card.Img variant="top" src={item.imageurl ? item.imageurl : noimage} height='250px' />
                <Card.Body>
                  <Card.Title>{item.article_title.slice(0, 50)}</Card.Title>
                  <Card.Text>
                    {item.description ? item.description.slice(0, 50) : 'Enter the button for further info of your Selected News'}
                  </Card.Text>
                  <h6 className="card-title text-secondary">{`By ${!item.publisher_name ? 'Unknown' : item.publisher_name}`}</h6>
                  <Link variant="danger" className='btn btn-danger' to={`/detail/${item.article_id}`} target='top'>More Detail</Link>
                </Card.Body>
              </Card>
            ))
        }
      </div>
    </div>
  )
}

export default ArticleShow;
