
import axios from 'axios';
import Button from 'react-bootstrap/Button';
import React, { useEffect, useState } from 'react';
import NewsItem from './NewsItem';
import Banner from '../Shared/layout/Banner.js'
import AOS from 'aos';
import 'aos/dist/aos.css';
import Spinner from './Spinner.js';
AOS.init();
const NewsContainer = (props) => {
  const [data, setData] = useState([]);
  const [page, setPage] = useState(1);
  const [results, setResults] = useState(null);
  const [loading, setLoading] = useState(false);
  const { category,count,country } = props;
  const UpdateNews = async () => {
    try {
      setLoading(true);
      const response = await axios.get(`https://newsapi.org/v2/top-headlines?country=${props.country}&category=${props.category}&apiKey=4aa1d079cc1f486aa300f481c28afe1f&page=${page}&pageSize=${props.pageSize}`);
      setData(response.data.articles);
      setResults(response.data.totalResults)
      setLoading(false);
    } catch (error) {
    }
  };
  useEffect(() => {
    UpdateNews();
    return () => {
    };
  }, []);

  async function handleNext() {
    setPage(page + 1);
    UpdateNews();
  }
  async function handlePrevious() {
    setPage(page - 1);
    UpdateNews();
  }
  return (
    <>
    <Banner/>
      <div className='container-lg my-5 py-5 d-flex flex-column cntr'>
        <div data-aos="fade-right" className='my-4'>
          <h1 className='text-center'>AK-News <span className='text-danger'>Top-Headlines</span> <span>Related to {category==='general' ? count : category }</span> </h1>
          {loading && <Spinner />}
        </div>
        <div className='row'>
          {!loading && data.map((item) => {
            return (
              <div className='col-sm-12 col-md-6 col-lg-4  flex-column flex-md-row d-flex justify-content-center' data-aos="fade-right" key={item.url}>
                <NewsItem url={item.url} source={item.source.name} author={item.author} Published={item.publishedAt} title={item.title} description={item.description} urlToImage={item.urlToImage} />
              </div>
            );
          })}
        </div>
      </div>
      <div className="container d-flex justify-content-between mb-3">
        <Button className='btn-danger' disabled={page <= 1} onClick={handlePrevious}>Previous Page</Button>
        <Button className='btn-danger' disabled={page + 1 > Math.ceil(results / 25)} onClick={handleNext}>Next Page</Button>
      </div>
    </>
  );
};

export default NewsContainer;
