import React from 'react'
import noimg from '../Assets/images/noimg.jpg'
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import Card from 'react-bootstrap/Card';
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();

const NewsItem = (props) => {
  const { title, description, urlToImage, Published, url, author,source} = props;
  const newdate = new Date(Published);
  return (
    <div >
    <Card className=' bg-light my-3 text-dark shadow-lg crd ' data-aos="fade-right" >
    <Card.Img variant="top" src={urlToImage ? urlToImage : noimg} height='250px' />
    <Card.Body>
    <Badge className='w-auto' bg="danger">{source}</Badge>
    <Card.Title>{title.slice(0, 50)}....</Card.Title>
          <Card.Text>
            {description ? description.slice(0, 50) : 'Enter the button for further info of your Selected News'}
          </Card.Text>
          <h6 className="card-title text-secondary">{`By ${!author?'Unknows':author} on ${newdate.toGMTString()}`}</h6>
          <div data-aos="zoom-in">
          <Button variant="danger" href={url} target='top'>More Detail</Button>
          </div>
        </Card.Body>
      </Card>

    </div>
  )
}

export default NewsItem






