import Carousel from 'react-bootstrap/Carousel';
import img from '../../Assets/images/Blue Daily News LinkedIn Banner (1).png';
import img1 from '../../Assets/images/Red and Black Modern Personal LinkedIn Banner.png';
import img2 from '../../Assets/images/Blue Daily News LinkedIn Banner (1).gif';


function DarkVariantExample() {
  return (
    <Carousel data-bs-theme="dark" className='container-lg  w-100 my-3' >
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={img2}
          alt="First slide"
        />
       
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={img1}
          alt="Second slide"
        />
        
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          src={img}
          alt="Third slide"
        />
      </Carousel.Item>
    </Carousel>
  );
}

export default DarkVariantExample;