import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { Link, useNavigate } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';

const Signup = () => {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [submit, setSubmit] = useState(false);

    useEffect(() => {
        if (submit) {
            const signUpData = { email, password };

            const sendSignUpData = async () => {
                try {
                    const response = await axios.post('https://junkawaydubai.com/api/signup.php', signUpData, {
                        headers: {
                            'Content-Type': 'application/json',
                        },
                    });
                    if (response.data) {
                        toast.success('Signup successful!', {
                            position: "top-right",
                            autoClose: 5000,
                            hideProgressBar: false,
                            closeOnClick: true,
                            pauseOnHover: true,
                            draggable: true,
                            progress: undefined,
                        });
                        setTimeout(() => navigate('/login'), 3000);
                    }

                } catch (error) {
                    toast.error('Signup failed. Please try again.', {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    console.error('Error:', error);
                } finally {
                    setSubmit(false); 
                }
            }
            sendSignUpData();
        }

    }, [submit, email, password]);

    const handleSubmit = (event) => {
        event.preventDefault();
        setSubmit(true);
    };
    return (
        <div className='cntr d-flex flex-column gap-4  justify-content-center align-items-lg-center' style={{ "height": '79vh' }}>
        <h1>SignUp Here</h1>
            <Form className='d-flex flex-column justify-content-center text-center gap-4 bg-light shadow-lg p-3 rounded-3' onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formBasicEmail">

                    <Form.Control type="email" placeholder="Enter email" value={email}
                        onChange={(e) => setEmail(e.target.value)} required />

                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">

                    <Form.Control type="password" placeholder="Password" value={password}
                        onChange={(e) => setPassword(e.target.value)} required />
                </Form.Group>
                <Button variant="danger" type="submit">
                    Submit
                </Button>
                <Link variant="danger" to='/login'>
                    LogIn
                </Link>
            </Form>
            <ToastContainer />
        </div>
    )
}

export default Signup
